const config = require('../config/index')
const { EVENT_TIMEOUT, EVENT_ERROR } = require('../data/events')
const { HTMLTAGS } = require('../data/index')

/**
 * 
 */
class FontManager {
  /**
   * @param {OptionsManager} manager 
   */
  constructor(manager) {
    this.manager = manager
    var fontName = this.manager.getSystemFontName()
    this.fontSets = [
      {
        fontName,
        fontUrl: config.FONT_BASE_URL + fontName,
        loaded: false
      }
    ]
  }

  addFontSet(fontName) {
    this.fontSets.push({
      fontName,
      fontUrl: config.FONT_BASE_URL + fontName,
      loaded: false
    })
  }

  /**
   * Unicode
   */
  loadSpadeFile() {
    return new Promise((resolve, reject) => {
      var xhr = new XMLHttpRequest()
      xhr.timeout = this.manager.getTimeout()
      xhr.open('GET', config.SPADES_URL, true)
      xhr.setRequestHeader('Content-Type', 'text/plain')
      xhr.onreadystatechange = function (){
        if(xhr.readyState === XMLHttpRequest.DONE){
          if(xhr.status === 200 || xhr.status == 304){
            resolve(xhr.responseText)
            // 
            console.log('loadSpadeFile success')
          }else{
            console.log('loadSpadeFile error')
            reject(EVENT_ERROR)
          }
        }
      }
      xhr.ontimeout = function() {
        console.log('loadSpadeFile ontimeout')
        reject(EVENT_TIMEOUT)
      }
      xhr.onerror = function() {
        console.log('loadSpadeFile onerror')
        reject(EVENT_ERROR)
      }
      xhr.send()
    })
  }

  /**
   * 
   */
  loadAllFontFile() {
    // fontSets
    var index = this.fontSets.findIndex((font) => {
      return !font.loaded
    })
    var fontUrl = this.fontSets[index]['fontUrl']
    try {
      if (document.fonts && (typeof FontFace == 'function')) { // FontFace
        var fontPromises = config.FONT_TYPES.map(fontType => {
          return new FontFace('pre_longhu_default_font', `url(${fontUrl}.${fontType})`).load()
        })
        return Promise.all(fontPromises).then(() => {
          return Promise.resolve([fontUrl])
        }).catch(() => {
          return Promise.reject(EVENT_ERROR)
        })
      } else {
        // get
        var promises = config.FONT_TYPES.map(fontType => {
          return this.loadFontFile(fontUrl, fontType)
        })
        return Promise.all(promises).then((data) => {
          console.log('fontfile success')
          return Promise.resolve(data)
        }).catch((eventName) => {
          console.log(' at fontManager loadAllFontFile')
          return Promise.reject(eventName)
        })
      }
    } catch (error) {
      console.log(' at fontManager loadAllFontFile')
      return Promise.reject(EVENT_ERROR)
    }
  }

  /**
   * 
   * @param {*} fontUrl 
   * @param {*} fontType 
   */
  loadFontFile(fontUrl, fontType) {
    return new Promise((resolve, reject) => {
      var xhr = new XMLHttpRequest()
      xhr.timeout = this.manager.getTimeout()
      xhr.open('GET', fontUrl + '.' + fontType, true)
      xhr.onreadystatechange = function (){
        if(xhr.readyState === XMLHttpRequest.DONE){
          if(xhr.status === 200 || xhr.status == 304){
            console.log('success')
            resolve(fontUrl)
          }else{
            console.log('onerror')
            reject(EVENT_ERROR)
          }
        }
      }
      xhr.ontimeout = function() {
        console.log('ontimeout')
        reject(EVENT_TIMEOUT)
      }
      xhr.onerror = function() {
        console.log('onerror')
        reject(EVENT_ERROR)
      }
      xhr.send()
    })
  }

  /**
   * unicode
   */
  addFontSpade(spades) {
    this.spades = JSON.parse(spades).map(function(charCode) {
      return String.fromCharCode(charCode)
    })
    console.log(this.spades)
    console.log('this.spades')
  }

  buildFontDeclareStyle(fontUrl, fontName) {
    // css
    var style = document.createElement('style')
    style.id = 'maia-copy-style'
    let fontFormat = '', fontFamilySrc = ''
    if (config.FONT_TYPES.indexOf('eot') !== -1) {
      fontFamilySrc = `src: url('${fontUrl}.eot');\nsrc: url('${fontUrl}.eot?#iefix') format('embedded-opentype'),\n`
    } else {
      fontFamilySrc = 'src: ' 
    }
    config.FONT_TYPES.forEach(fontType => {
      if (fontType == 'eot') {
        fontFamilySrc += ''
      } else {
        fontFormat = fontType === 'ttf' ? 'truetype' : fontType 
        fontFamilySrc += `url('${fontUrl}.${fontType}') format('${fontFormat}'),\n`
      }
    })
    fontFamilySrc = fontFamilySrc.replace(/\,\n$/, ';')
    var fontDeclare = `@font-face { font-family: 'longhu-default-font'; ${fontFamilySrc}}`
    var oldFontFamily = getComputedStyle(document.body).fontFamily
    var newFontFamily = '"longhu-default-font", ' + oldFontFamily
    // html
    style.innerHTML = fontDeclare + `\n ${HTMLTAGS} {font-family: ${newFontFamily};}`
    document.head.appendChild(style)
  }

  /**
   * css
   * @param {*} ruleName 
   * @param {*} fontName 
   */
  addFontDeclare(ruleName, fontName) {
    // css
    let styleTAG = document.getElementById('maia_copy_custom_style')
    let oldStyleRules = ''
    if (styleTAG) {
      oldStyleRules = styleTAG.textContent
      document.head.removeChild(styleTAG)
    }
    var fontUrl = config.FONT_BASE_URL + fontName
    var promises = config.FONT_TYPES.map(fontType => {
      return this.loadFontFile(fontUrl, fontType)
    })
    return Promise.all(promises).then(() => {
      // fontName
      this.fontSets.push({
        fontName,
        fontUrl: config.FONT_BASE_URL + fontName,
        loaded: true
      })
      let fontFormat = '', fontFamilySrc = ''
      if (config.FONT_TYPES.indexOf('eot') !== -1) {
        fontFamilySrc = `src: url('${fontUrl}.eot');\nsrc: url('${fontUrl}.eot?#iefix') format('embedded-opentype'),\n`
      } else {
        fontFamilySrc = 'src: ' 
      }
      config.FONT_TYPES.forEach(fontType => {
        if (fontType == 'eot') {
          fontFamilySrc += ''
        } else {
          fontFormat = fontType === 'ttf' ? 'truetype' : fontType 
          fontFamilySrc += `url('${fontUrl}.${fontType}') format('${fontFormat}'),\n`
        }
      })
      fontFamilySrc = fontFamilySrc.replace(/\,\n$/, ';')
      var fontDeclare = `@font-face { font-family: 'longhu-custom-${fontName}'; ${fontFamilySrc}}`
      var oldFontFamily = getComputedStyle(document.body).fontFamily
      var newFontFamily = `"longhu-custom-${fontName}", ${oldFontFamily}`
      var styleRules = oldStyleRules + '\n' + fontDeclare + `\n ${ruleName} {font-family: ${newFontFamily};}`
      styleTAG = document.createElement('style')
      styleTAG.id = 'maia_copy_custom_style'
      styleTAG.textContent = styleRules
      document.head.appendChild(styleTAG)
      return Promise.resolve()
    }).catch((err) => {
      return Promise.reject(err)
    })
  }

  addFontSelector(ruleName) {
    let styleTAG = document.getElementById('maia_copy_custom_style')
    let oldFontFamily = getComputedStyle(document.body).fontFamily
    let newFontFamily = '"longhu-default-font", ' + oldFontFamily
    let oldStyleRules = ''
    if (styleTAG) {
      oldStyleRules = styleTAG.textContent
      document.head.removeChild(styleTAG)
    }
    let styleRules = oldStyleRules + `\n
      ${ruleName} {
        font-family: ${newFontFamily};
      }
    `
    styleTAG = document.createElement('style')
    styleTAG.id = 'maia_copy_custom_style'
    styleTAG.textContent = styleRules
    document.head.appendChild(styleTAG)
  }

  getAllFontNames() {
    var fontNames = []
    this.fontSets.forEach((font) => {
      if (font.loaded) {
        fontNames.push(font.fontName)
      }
    })
    return fontNames
  }
}

module.exports = FontManager