const Transfer = require('./transfer')
const Utils = require('../utils/index')
const config = require('../config')
const { EVENT_CONNECTED, EVENT_TIMEOUT, EVENT_ADD_FONT_RULE, EVENT_REMOVE, EVENT_ERROR } = require('../data/events')
const DeviceDetect = require('./deviceDetect')

class CopyListener {
  #transfer = null
  #loaded = false
  #error = false
  #recorder = null
  #fontManager = null
  constructor(recorder, fontManager) {
    this.#recorder = recorder
    this.#fontManager = fontManager // 
    this.eventQueue = {} // 
    this.domSelector = new Set()
    var promises = []
    if (!DeviceDetect.isServerTransfer()) {
      promises.push(this.#fontManager.loadSpadeFile()) // unicode
    }
    promises.push(this.#fontManager.loadAllFontFile()) // 
    Promise.all(promises).then(([encodeText, fontUrls]) => {
      this.#fontManager.addFontSpade(encodeText)
      this.#transfer = new Transfer(this.#recorder, this.#fontManager)
      // css @font-face
      if (document.body) {
        this.#fontManager.buildFontDeclareStyle(fontUrls[0]) // url
      } else {
        document.addEventListener('DOMContentLoaded', function() {
          this.#fontManager.buildFontDeclareStyle(fontUrls[0]) // url
        })
      }
      this.#loaded = true
      console.log(`: ${this.#recorder.abbrGlyphCode}`)
      // 
      this.#recorder.record().catch((errType) => {
        this.#transfer.update(true)
      })
    }).catch(err => {
      console.log(err)
      console.log(' at copy listener class')
      this.#error = true
      // 
    })
    // timeout
    this.on(EVENT_TIMEOUT, function(err, text) {
      if (err) {
        console.log('event timeout')
        console.warn(err)
        return text
      }
    })
  }

  get connectStatus() {
    return this.#loaded
  }

  /**
   * 
   */
  connect() {
    //  
    var timeStep = 50, waitTime = 0
    if (this.#transfer && this.#loaded) {
      // 
      return Promise.resolve(this.#transfer)
    } else {
      return new Promise((resolve, reject) => {
        var timer = setInterval(() => {
          if (this.#transfer && this.#loaded) {
            clearInterval(timer)
            this._trigger(EVENT_CONNECTED, [null, this.#transfer])
            resolve(this.#transfer) // 
          } else {
            waitTime += timeStep
          }
          if (this.#error) { // 
            reject(EVENT_ERROR)
            this._trigger(EVENT_ERROR)
          } else if (waitTime >= this.#fontManager.manager.getTimeout()) { // 
            this._trigger(EVENT_TIMEOUT)
            reject(EVENT_TIMEOUT)
          }
        }, timeStep) // 50ms
      })
    }
  }

  /**
   * css
   * @param {*} selector 
   * @param {*} fontName () ()
   * @param {*} timeout 
   */
  addRule(selector, fontName) {
    // domSelector
    var oldLength = this.domSelector.size
    this.domSelector.add(selector)
    if (this.domSelector.size > oldLength) { // new Rule
      // fontNamesfontName 
      if (fontName && (this.#fontManager.getAllFontNames().indexOf(fontName) === -1)) { // 
        return this.#fontManager.addFontDeclare(selector, fontName).then(() => {
          console.log('EVENT_ADD_FONT_RULE')
          this._trigger(EVENT_ADD_FONT_RULE, [null, fontName])
          return Promise.resolve()
        }).catch(eventName => {
          this._trigger(eventName, eventName)
          return Promise.reject(eventName)
        })
      } else {
        // css
        this.#fontManager.addFontSelector(selector)
        return Promise.resolve()
      }
    } else {
      return Promise.reject('')
    }
  }

  transform(text) {
    if (!DeviceDetect.isValidSystem() || DeviceDetect.isServerTransfer()) { // 
      return text
    } else {
      if (this.#transfer && this.#loaded) {
        return this.#transfer.transform(text)
      } else {
        return text
      }
    }
  }

  update(local) {
    if (this.#transfer && this.#loaded) {
      this.#transfer.update(local)
    } else {
      this._trigger(EVENT_TIMEOUT, '')
    }
  }

  remove() {
    this._trigger(EVENT_REMOVE)
  }

  /**
   * binding event handler
   * @param {*} eventName 
   * @param {*} callback 
   */
  on(eventName, callback) {
    if (!this.eventQueue[eventName]) {
      this.eventQueue[eventName] = []
    }
    this.eventQueue[eventName].push(callback)
  }

  /**
   * trigger event
   * @param {*} eventName 
   * @param {*} eventParams 
   * @param {*} defaultHandler 
   */
  _trigger(eventName, eventParams, defaultHandler) {
    if (this.eventQueue[eventName] && this.eventQueue[eventName].length) {
      for(let i = 0; i < this.eventQueue[eventName].length; i++) {
        var handler = this.eventQueue[eventName][i]
        if (typeof handler == 'function') {
          if (typeof defaultHandler == 'function') {
            if (Array.isArray(eventParams)) {
              defaultHandler(...eventParams)
            } else {
              defaultHandler(eventParams)
            }
          }
          if (Array.isArray(eventParams)) {
            handler(...eventParams)
          } else {
            handler(eventParams)
          }
        }
      }
    }
  }
}

module.exports = CopyListener