const config = require('./config')
const CopyListener = require('./core/copyListener')
const Utils = require('./utils/index')
const Recorder = require('./core/recorder')
const { EVENT_ADD_FONT_RULE, EVENT_REMOVE } = require('./data/events')
const FontManager = require('./core/fontManager')
const OptionsManager = require('./config/option')

/**
 * options:
 * {
 *    fontName: (MicrosoftYahei) // 
 *    winFontName: windows(MicrosoftYahei)
 *    macFontName: mac(PingfangSC)
 *    androidFontName: android(MicrosoftYahei)
 *    iosFontName: ios(PingfangSC)
 *    timeout: 
 * 
 *    system: (window/mac/android/ios)
 * }
 */
const createCopyListener = (function(){
  var listener
  function createCopyListener(oaCount, appKey, options) {
    if(listener) {
      console.warn('')
      return listener
    }
    // recorder, optionsManager, fontManager, listener
    const recorder = new Recorder(oaCount, appKey)
    if (options['customCode']) {
      recorder.setMode('custom', options['customCode'])
    }
    const optionsManager = new OptionsManager(options)
    const fontManager = new FontManager(optionsManager)
    listener = new CopyListener(recorder, fontManager)

    listener.on(EVENT_ADD_FONT_RULE, function(err, newFontName) {
      if (!err) {
        console.log(`: ${newFontName} `)
        // recorder.record().catch(() => {
        //   // listener.update(true)
        // })
      }
    })

    listener.on(EVENT_REMOVE, function() {
      listener = null
    })
    return listener
  }
  return createCopyListener
})()

module.exports = createCopyListener
