const Utils = {
  isObject(obj) {
    return Object.prototype.toString.call(obj) === '[object Object]'
  },
  isNotEmptyString(str) {
    return typeof str === 'string' && str !== ''
  },
  isNotString(str) {
    return typeof str !== 'string'
  },
  isNotNaNNumber(num) {
    return typeof num !== 'number' && Number.isNaN(num)
  }
}

module.exports = Utils