const Utils = require('../utils/index.js')
const config = require('../config/index')
const DeviceDetect = require('./deviceDetect.js')
class Transfer {
  #recorder
  #fontManager
  #spades
  #glyphCode
  #pointer = 0
  /**
   * 
   * @param {*} recorder Recorder
   * @param {*} fontManager FontManager
   */
  constructor(recorder, fontManager) {
    this.#recorder = recorder
    this.#fontManager = fontManager
    this.#glyphCode = this.#recorder.generateGlyphCode()
    this.#spades = this.#fontManager.spades
  }

  /**
   * unicode
   * @param {*} text 
   */
  transform(text) {
    // / 
    if (!DeviceDetect.isValidSystem() || DeviceDetect.isServerTransfer()) {
      return text
    }
    var transCount = 0, charCount = 0, processText = '', textObj = [], execRes, splitHtml, splitIndex, presplitIndex = 0
    const cjkCharReg = new RegExp('[\\u4E00-\\u9FFF]', 'i')
    // texthtml
    var r = /<\/?.+?\/?>/g
    if (/<\/?.+?\/?>/g.test(text)) {
      while(execRes = r.exec(text)) {
        // html,
        var splitHtml = execRes[0]
        var splitIndex = execRes.index
        var part = text.slice(presplitIndex, splitIndex)
        presplitIndex = splitIndex + splitHtml.length
        textObj.push({
          text: part,
          transform: true
        })
        textObj.push({
          text: splitHtml,
          transform: false
        })
      }
      // 
      textObj.push({
        text: text.slice(presplitIndex),
        transform: true
      })
    } else {
      textObj.push({
        text,
        transform: true
      })
    }
    var char, glyphIndex, charUnicode
    for(let j = 0; j < textObj.length; j++) {
      if (textObj[j].transform) {
        for(let i = 0; i < textObj[j].text.length; i++) {
          char = textObj[j].text[i]
          glyphIndex = 0
          charUnicode = Number(char.charCodeAt(0)).toString(16)
          if (!cjkCharReg.test(char)) {
            // unicode/\u4E00-\u9FFF/
            processText += ('&#x' + charUnicode)
            continue
          }
          if (this.#spades.indexOf(char) !== -1) { // 
            glyphIndex = Number(this.#glyphCode[this.#pointer])
            // ->->code
            this.#pointer = (this.#pointer + 1) % this.#glyphCode.length
            processText += ('&#x' + charUnicode + glyphIndex)
            transCount++ // 
          } else { // 
            processText += ('&#x' + charUnicode)
          }
          charCount++ // 
        }
      } else {
        // html
        processText += textObj[j].text
      }
    }
    this.#recorder.recordGlyphCount(transCount, charCount) // 
    return processText
  }

  update(local) {
    this.#glyphCode = local ? this.#recorder.generateLocalGlyphCode() : this.#recorder.generateGlyphCode()
    this.#pointer = 0 // 
    if (!local) {
      this.#recorder.record().catch((errType) => {
        if (config.IS_OPEN_NON_NETWORK) { // 
          this.#glyphCode = this.#recorder.generateLocalGlyphCode()
          console.log(`no network, glyphCode = ${this.#glyphCode}`)
        }
        // 
        console.log(errType)
        this.#recorder.recordError(errType).catch(() => {
  
        })
      })
    }
  }
}

module.exports = Transfer